const PNG_SUCCESS = 'data:image/png;base64,';
const PNG_LOADING = 'data:image/png;base64,';


export class IconView extends Laya.Sprite {

    private _image: Laya.Image;
    private _angle = 0;

    constructor() {
        super();
        this._image = new Laya.Image;
        this.addChild(this._image);
    }

    public setSize(size) {
        this.width = size;
        this.height = size;
        this.pivot(size / 2, size / 2);
    }

    public setIcon(icon) {
        this._image.visible = true;
        this._image.width = this.width;
        this._image.height = this.height;
        this._image.skin = this._getIconUrl(icon);
        this._stop();
        if (icon == 'loading') {
            this._play();
        }
    }

    private _play() {
        this.timer.frameLoop(1, this, this._update);
    }

    private _stop() {
        this.timer.clear(this, this._update);
        this._angle = 0;
        this.rotation = this._angle;
    }

    private _getIconUrl(icon) {
        if (icon == 'loading') {
            return PNG_LOADING;
        } else if (icon == 'success') {
            return PNG_SUCCESS;
        } else {
            return icon;
        }
    }

    private _update() {
        this._angle += 5;
        if (this._angle >= 360) {
            this._angle = this._angle - 360;
        }
        this.rotation = this._angle;
    }

}